	module.exports = {"data":{"site":{"siteMetadata":{"title":"Patrik Mihalcin blog","author":"Patrik Mihalcin"}},"markdownRemark":{"id":"C:/devel/Projects/pmihalcin-blog/src/pages/2017-11-11-first-one/index.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>write tests along with new functionality; don’t leave it for later</li>\n<li>tests should be on all levels, all slices (units, services, controllers)</li>\n<li>new shiny technologies create new problems </li>\n<li>\n<p>be prepared to discuss problems with authors of frameworks </p>\n<ul>\n<li>it’s great that spring boot uses <a href=\"https://gitter.im/spring-projects/spring-boot\">gitter channel</a> and <a href=\"https://github.com/spring-projects/spring-boot\">github</a> to track issues</li>\n<li>on the other hand I hate solving problems with Oracle support, which is proprietary shite</li>\n</ul>\n</li>\n<li>prefer to know how the libaries you’re using actually work rather than just taking them as given     </li>\n<li>when starting out in new company, assess all aspects, e.g. what application server you are going to use, you don’t want to be solving weblogic’s crazy classpath issues</li>\n<li>go for simple code, simple software, have high ambitions</li>\n<li>discuss with team leader that even though it seems it is going to be simple system, you spend time solving technical problems instead of business ones initially</li>\n<li>you should know when perfectionism is the enemy of delivered</li>\n<li>use pragmatic approach to complex problems</li>\n</ul>","frontmatter":{"title":"Create new system | LESSONS LEARNT","date":"November 11, 2017"}}},"pathContext":{"path":"/create-new-system-lessons-learnt/"}}

